'use strict';
var controller;
controller=angular.module("App.popup.controllers",[]);
controller.controller("PopupCtrl",[
    "$scope","$rootScope",function($scope,$rootScope){
        $scope.openPopup=function(msg,data){
            return $rootScope.$broadcast('popup',{
                data:data,
                msg:msg,
                isOpened:true
            });
        };
    }
]);


